* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
  color: #333333;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  background-color: #eaecf0;
}

html {
  -webkit-text-size-adjust: 100%;
  scrollbar-width: thin;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
button,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  line-height: 0;
  touch-action: manipulation;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
  appearance: textfield;
}

table {
  margin-bottom: unset !important;
}

.form-check-input:disabled {
  background-color: #aaaaaa;
  border-color: #aaaaaa;
  opacity: 1;
}

::selection {
  background-color: #0050b3;
  color: #fffcfa;
}

.alert-message {
  margin-block: 12px;
  padding: 8px 12px;
  background: #e6f4ff;
  border: 1px solid #91caff;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  border-radius: 8px;
}
.alert-message i {
  color: #1677ff;
  margin-right: 8px;
}
.alert-message a {
  margin-left: 2px;
  color: #0050b3;
}
.alert-message a:hover {
  opacity: 0.8;
}

@media only screen and (max-width: 1199px) {
  #wrapper > *:not(.message-mobile) {
    display: none !important;
  }
}
.header {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  padding: 10px 0 20px 0;
  background-color: #eaecf0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: clamp(1.563rem, -18.125rem + 21.875vw, 3.75rem);
}
.header .wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}
.header .wrapper .logo-web {
  font-weight: 500;
  font-size: 64px;
  line-height: 96px;
  color: #333333;
  transition: color 0.24s ease;
}
.header .wrapper .logo-web:hover {
  color: rgb(175, 65, 65);
}
.header .wrapper img {
  width: 45px;
  cursor: pointer;
}
.header .variation-link {
  display: flex;
  align-items: center;
}
.header .variation-link a {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 15px;
  border-bottom: 2px solid transparent;
  color: rgb(16, 24, 40);
}
.header .variation-link a svg {
  width: 17px;
}
.header .variation-link a span {
  font-size: 16px;
}
.header .variation-link a.active {
  color: rgb(0, 80, 179);
  border-color: #096dd9;
}
.header .search {
  background-color: #dbdfe6;
  display: flex;
  align-items: center;
  gap: 10px;
  flex: 1;
  height: 34px;
  border-radius: 8px;
}
.header .search-dropdown {
  max-width: clamp(6.25rem, -62.973rem + 81.081vw, 10rem);
  width: 100%;
  border-right-color: #d0d5dd;
  display: flex;
  align-items: center;
  gap: 20px;
  justify-content: center;
  height: 100%;
  cursor: pointer;
}
.header .search-dropdown i {
  color: rgb(191, 189, 189);
}
.header .search-field {
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
  flex: 1;
  height: 100%;
  padding-inline: 20px;
  border-left: 1px solid #d0d5dd;
  transition: all 0.25s ease;
  border-radius: inherit;
}
.header .search-field:focus-within {
  border-color: #86b7fe;
  box-shadow: 0 0 0 0.25rem rgba(14, 110, 253, 0.25);
}
.header .search-field input {
  font-family: inherit;
  font-size: 14px;
  line-height: 22px;
  border: none;
  outline: none;
  background-color: transparent;
  width: 100%;
  height: 100%;
}
.header .control {
  max-width: clamp(21.875rem, -35.811rem + 67.568vw, 25rem);
  width: 100%;
  gap: 18px;
}
.header .control, .header .control-main, .header .control-filter {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header .control-filter {
  justify-content: space-between;
  gap: 10px;
  background-color: #dbdfe6;
  padding: 11px 12px;
  max-width: 180px;
  width: 100%;
  border-radius: 6px;
}
.header .control-filter i {
  color: rgb(191, 189, 189);
}
.header .control-main {
  gap: 18px;
}
.header .control-main svg {
  width: 20px;
  cursor: pointer;
}
.header .control-main .avatar {
  border-radius: 50%;
  width: 32px;
  height: 32px;
  background-color: hsl(357, 50%, 50%);
  display: inline-block;
  line-height: 2.5;
  text-align: center;
  color: #fff;
  font-size: 12.8px;
  cursor: pointer;
}

.sidebar {
  position: sticky;
  top: 0;
  height: 100vh;
  max-width: 190px;
  width: 100%;
  background-color: #f2f4f7;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
}
.sidebar-logo {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 18px;
  padding: 16px;
}
.sidebar-logo span {
  width: 64px;
  height: 64px;
  border: 1px solid rgb(208, 213, 221);
  background-color: rgb(174, 64, 191);
  display: inline-block;
  line-height: 2.5;
  text-align: center;
  color: rgb(255, 255, 255);
  font-size: 25.6px;
  box-sizing: content-box;
  border-radius: 50%;
}
.sidebar-logo h4 {
  color: #101828;
  font-size: 14px;
  font-weight: 600;
}
.sidebar-menu > ul > li > a.active {
  color: #0050b3;
}
.sidebar-menu ul li a {
  color: #101828;
  line-height: 22px;
  padding: 10px 20px;
  font-size: 14px;
  display: inline-flex;
  align-items: center;
  gap: 11px;
  width: 100%;
  transition: all 0.24s ease;
}
.sidebar-menu ul li a:hover {
  color: #40a9ff;
}
.sidebar-menu ul li a svg {
  font-size: 20px;
}
.sidebar-menu ul li a i {
  margin-left: auto;
  font-size: 11px;
}
.sidebar-menu .submenu li a {
  padding-left: 2.4rem !important;
}
.sidebar-menu .submenu li a.active {
  pointer-events: none;
  background-color: #d6e3fb;
  border-radius: 8px;
  font-weight: bold;
}
.sidebar-footer {
  border-top: 1.5px solid rgb(208, 213, 221);
  margin-top: auto;
  padding: 10px 20px;
}
.sidebar-footer button {
  padding: 5px;
  display: flex;
  align-items: center;
  gap: 10px;
}
.sidebar-footer button svg {
  width: 25px;
}
.sidebar-footer button span {
  color: #101828;
  line-height: 22px;
  font-size: 14px;
  display: inline-flex;
  align-items: center;
  gap: 11px;
  width: 100%;
  transition: all 0.24s ease;
}

.tools {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  gap: 10px 20px;
}
.tools-add {
  background-color: #0050b3;
  border-radius: 8px;
  color: #fffcfa;
  padding: 9.1px 12px;
  transition: opacity 0.24s ease;
}
.tools-add:hover {
  opacity: 0.8;
}
.tools-add i {
  font-size: 14px;
}
.tools-button, .tools-group {
  background-color: #ffffff;
  border: 1px solid #d0d5dd;
  display: flex;
  gap: 8px;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  white-space: nowrap;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  color: #101828;
  border-radius: 6px;
  padding: 9.1px 12px;
}
.tools-button:hover, .tools-group:hover {
  color: #40a9ff;
  border-color: #40a9ff;
}
.tools-button svg, .tools-group svg {
  width: 14px;
}
.tools-button i, .tools-group i {
  font-size: 10px;
  color: inherit;
}
.tools-button .eye, .tools-group .eye {
  font-size: 14px;
  color: #40a9ff;
}
.tools-button .line, .tools-group .line {
  height: 100%;
  background-color: red;
  width: 1px;
}
.tools-group {
  padding: 0 12px;
}
.tools-group:hover {
  border-color: transparent;
  color: unset;
}
.tools-group .line {
  height: 100%;
  width: 1px;
  background-color: #d0d5dd;
}

.table thead tr th {
  background-color: #dee1e8;
}
.table th,
.table td {
  text-align: center;
  padding: 14px;
}